import React from 'react';
import './Footer.css';
import home from '../images/home.png';
import activity from '../images/activity.png';
import market from '../images/market.png';
import wallet from '../images/wallet.png';
import earn from '../images/earn.png'

export default function Footer() {
    return (
        <div>
        <div className='footerContainer'>
            <a>
                <img alt='home'  src={home} width='35px'  height='35px'/>
                <h5>Home</h5>
            </a>
            <a className='img'>
                <img alt='activity' src={activity} width='35px'  height='35px'/>
                <h5>Activity</h5>
            </a>
            <a className='img'>
                <img alt='market' src={market} width='35px'  height='35px'/>
                <h5>Market</h5>
            </a>
            <a className='img'>
                <img alt='wallet' src={wallet} width='35px'  height='35px'/>
                <h5>Wallet</h5>
            </a>
            <a className='img'>
                <img alt='earn' src={earn} width='35px'  height='35px'/>
                <h5>Earn</h5>
            </a>
        </div>
        </div>
    )
}
