import React from 'react';
import './Token.css'

export default function Token() {
    return (
        <div>
            <div className="currency">
                <h4>Select Currency:</h4>
            </div>
            <div className=" token">
            <label>TOKEN</label>
            <input 
            type="number"
            placeholder="0.00000001"/>
            </div>
            <div className="button">
                <button>SEND</button>
                <button>RECIEVE</button>
            </div>
        </div>
    )
}
